<template>
<div class="Institutional-investment">
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Institution_number")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col> 
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Institution_name")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.User_source")}}&nbsp</p>
        <Select style="width:150px;margin-right:10px;">
            <Option v-for="item in cityList" :value="item.value" :label="$t(item.label)" :key="item.value"></Option>
        </Select>
        <Button type="primary">{{$t("common.Query")}}</Button>
      </Col>
  </Row>
  <Table highlight-row :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  <Row type="flex" justify="center" class="code-row-bg page-data">
    <Col span="24">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row>
</div>
</template>

<script>
export default {
    name:'Institutional_investment',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            cityList: [
                {
                    value: 'All',
                    label: 'common.All'
                },
                {
                    value: 'Yes',
                    label: 'common.On_line'
                },
                {
                    value: 'No',
                    label: 'common.Offline'
                }
            ],
            model1: '',
            columns1: [
                {
                    title: this.$t("common.Institution_number"),
                    key: 'user_number',
                    align:'center'
                },
                {
                    title: this.$t("common.Institution_name"),
                    key: 'name',
                    align:'center',
                    width:200
                },
                {
                    title: this.$t("common.Registered_address"),
                    key: 'address',
                    align:'center',
                    width:200
                },
                {
                    title: this.$t("common.User_source"),
                    key: 'source',
                    align:'center'
                },
                {
                    title: this.$t("common.Registration_date"),
                    key: 'date',
                    align:'center'
                },
                {
                    title: this.$t("common.login_time"),
                    key: 'login_time',
                    align:'center'
                }
            ],
            data1: this.mockTableData1 (),
            Personal_info:null
        }
    },
    methods:{
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              data.push({
                  user_number: 'DG0000'+i,
                  name: parseInt(Math.random()*(3-1)+0)>0?'工商银行上海自贸区支行':'广发银行南京鼓楼支行',
                  address: parseInt(Math.random()*(3-1)+0)>0?'上海市浦东新区徐汇路321号':'南京市进香河路33-1号',
                  source:parseInt(Math.random()*(3-1)+0)>0?'线下':'线上',
                  date:this.formatTime(this.getRandomDateBetween()),
                  login_time:this.formatDate(this.getRandomDateBetween())
              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      formatTime (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          return y + '-' + m + '-' + d;
      },
      onedata (data,index){
        //console.log(data,index)
        this.Personal_info = data
        this.sendParams ()
      },
      sendParams () {
          this.$router.push({
              path: '/Institutional_info',
              name: 'Institutional_info',
              params: {
                  name: 'Personal-info',
                  send_data: this.Personal_info
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       }
    },
    created(){
      this.handleListApproveHistory();
    }
}
</script>

<style lang="css">
.Institutional-investment{
  
}
.Institutional-investment .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
.Institutional-investment .search_criteria p{
  height: 30px;
}
.Institutional-investment .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  background-color: #fff;
  text-align: center;
}
</style>



